import React, { useState, useEffect } from 'react';
import supabase from './supabase';
import '../style/players.css';

const PlayersForm = () => {
    const [name, setName] = useState('');
    const [handicap, setHandicap] = useState('');
    const [availability, setAvailability] = useState(false);
    const [players, setPlayers] = useState([]);

    useEffect(() => {
        fetchPlayers();
    }, []);

    const fetchPlayers = async () => {
        try {
            const { data, error } = await supabase.from('players').select('*');

            if (error) {
                throw error;
            }

            // Sort players alphabetically by name
            const sortedPlayers = data.sort((a, b) => a.name.localeCompare(b.name));
            setPlayers(sortedPlayers);
        } catch (error) {
            console.error('Error fetching players:', error.message);
        }
    };

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            const { data, error } = await supabase.from('players').insert([
                { name, handicap, availability },
            ]);

            if (error) {
                throw error;
            }

            console.log('Player data inserted successfully:', data);
            // After inserting new player, fetch updated list of players
            fetchPlayers();
        } catch (error) {
            console.error('Error inserting player data:', error.message);
        }
    };

    const handleAvailabilityToggle = async (playerId, newAvailability) => {
        try {
            const { data, error } = await supabase
                .from('players')
                .update({ availability: newAvailability })
                .eq('id', playerId);

            if (error) {
                throw error;
            }

            console.log('Availability updated successfully:', data);
            // After updating availability, fetch updated list of players
            fetchPlayers();
        } catch (error) {
            console.error('Error updating availability:', error.message);
        }
    };

    return (
        <div className="players-container">
            <h2>Add New Player</h2>
            <form onSubmit={handleSubmit} className="players-form">
                <label className="form-label">
                    Name:
                    <input
                        type="text"
                        value={name}
                        onChange={(e) => setName(e.target.value)}
                        className="form-input"
                    />
                </label>
                <br />
                <label className="form-label">
                    Handicap:
                    <input
                        type="number"
                        value={handicap}
                        onChange={(e) => setHandicap(Number(e.target.value))}
                        className="form-input"
                    />
                </label>
                <br />
                <label className="form-label">
                    Availability:
                    <input
                        type="checkbox"
                        checked={availability}
                        onChange={(e) => setAvailability(e.target.checked)}
                        className="form-checkbox"
                    />
                </label>
                <br />
                <button type="submit" className="submit-button">Submit</button>
            </form>

            <h2 className="players-list-title">Players List</h2>
            <table className="players-table">
                <thead>
                    <tr>
                        <th className="table-header">Index</th>
                        <th className="table-header">Name</th>
                        <th className="table-header">Handicap</th>
                        <th className="table-header">Availability</th>
                    </tr>
                </thead>
                <tbody>
                    {players.map((player, index) => (
                        <tr key={player.id} className="table-row">
                            <td className="table-data">{index + 1}</td>
                            <td className="table-data">{player.name}</td>
                            <td className="table-data">{player.handicap}</td>
                            <td className="table-data">
                                <input
                                    type="checkbox"
                                    checked={player.availability}
                                    onChange={(e) =>
                                        handleAvailabilityToggle(player.id, e.target.checked)
                                    }
                                    className="form-checkbox"
                                />
                            </td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    );
};

export default PlayersForm;
